import React from 'react';
import { Page } from '../../common/components/page';
import { WorkTitle } from '../../common/components/work-title';
import { Experience, Experiences } from './components/experience';

interface PageExperienceProps {
}

export const PageExperience = ({ }: PageExperienceProps) => {
  return (
    <Page className="h-min">
      <div className="card">
        <WorkTitle>EXPERIENCE</WorkTitle>
        <Experiences>
          <div>
            <Experience type="work" length={`2022 Sep\n2023 Mar`} experience="Frontend Developer" place="TEKsystems at Apple" link="https://www.teksystems.com/" />
            <Experience type="work" length={`2020 Sep\n2022 Aug`} experience="Frontend Developer" place="Skeleton Crew Studio" link="https://www.skeletoncrew.co.jp/" />
            <Experience type="internship" length={`2018 Jan\n2018 Dec`} experience="Frontend Developer Internship" place="University of Granada" link="https://www.ugr.es/en" />
            <Experience last type="graduation" length={`2015 Sep\n2019 Nov`} experience="Bachelor in Computer Science" place="University of Granada" link="https://www.ugr.es/en" />
          </div>
        </Experiences>
      </div>
    </Page>
  );
}

